<template>
  <div class="main-div">
    <div class="logo"></div>
    <div class="description-container">
      <p id="question">現在時間是早上7點，你剛從基隆出發，要去台北火車站接朋友。你朋友說他7:50會到，不想遲到的你，請問要如何搭乘？</p>
    </div>
    <div>
      <p id="answer-title">輸入班次代碼</p>
    </div>
    <div id="answer"><input type="text" v-model="answer"></div>
    <div id="button" @click="submitPressed"></div>
    <Correct v-if="showCorrectMessage" />
    <Wrong v-if="showWrongMessage" />
  </div>
</template>

<script>
import Correct from '@/components/TransferCorrect'
import Wrong from '@/components/TransferWrong'

export default {
  name: 'TransferDiagramOne',
  components: {
    Correct,
    Wrong
  },
  data () {
    return {
      window: {
        height: window.innerHeight,
        width: window.innerWidth
      },
      showCorrectMessage: false,
      showWrongMessage: false,
      answer: ''
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
    this.$store.commit('showFooter', false)
  },
  methods: {
    submitPressed () {
      const lowerCaseAnswer = this.answer.trim().toLowerCase()
      if (lowerCaseAnswer === 'tr07' || lowerCaseAnswer === 'kt07') {
        console.log('correct')
        this.showCorrectMessage = true
        setTimeout(this.gotoNextQuestion, 1500)
      } else {
        console.log('wrong')
        this.showWrongMessage = true
        setTimeout(this.removeWrongMessage, 1000)
      }
    },
    gotoNextQuestion () {
      this.$router.push({name: 'CommuteTransferDiagramTwo'})
    },
    removeWrongMessage () {
      this.showWrongMessage = false
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    min-height: 100vh;
    width: 100%;
    background-color: black;
    text-align: center;
    position: relative;
    .logo{
      @extend centerContainImage;
      margin-top: 15%;
      display: inline-block;
      width: 200px;
      height: 200px;
      background-image: url('~@/assets/images/commute/transfer-diagram1.png');
    }
    #question {
      @extend transferQuestionGreenText;
    }

    #answer-title {
      @extend transferAnswerTitleWhiteText;
    }

    #button {
      display: inline-block;
      margin-top: 15px;
      width: 45px;
      height: 45px;
      background-image: url('~@/assets/images/go-right-arrow.png');
      background-size: contain;
    }
  }
</style>



